body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.hex-dump-container {
    min-width: 797px;
    /*border: 1px solid red;*/
    font-size: 15px;
    line-height: 16px;
}

.hex-dump-panel {
    margin:auto;width:797px;
}

.hex-dump-panel span {
    font-size: 16px;
    line-height: 16px;
}

.hex-dump-offset-panel {
    width: 150px;
    height: 100%;
    margin: 5px;
    background-color: #F5f5f5;
    float:left;
}

.hex-dump-offset-panel span {
    font-family: monospace;
    padding:2px;
    float:left;
}

.hex-dump-body-panel {
    width: 402px;
    height: 100%;
    margin: 5px;
    background-color: #F5f5f5;
    float:left;
}
.hex-dump-body-panel span {
    font-family: monospace;
    padding: 2px;
    width: 21px;
    display: inline;
    float:left;
    margin-left: 2px;
    margin-right: 2px;
}
.hex-dump-characters-panel {
    margin: 5px;
    background-color: #f5f5f5;
    float:left;
    width: 195px;
    height: 100%;
}

.hex-dump-characters-panel span {
    font-family: monospace;
    padding:2px;
    width: 12px;
    display: inline;
    float:left;
}

.selected {
    background-color: #268 !important;
    color: #fff;
}

.clearer {
    clear: both;
}

.strip-odd {
    background-color: #f5f5f5;
}

.strip-even {
    background-color: #e5e5e5;
}
